import React, { useState, useEffect } from 'react'
import NormalButton from '../parts/button/button';
import CheckBox from '../parts/check_box/check_box';
import Pager from '../parts/pager/pager';
import Selector from '../parts/selector/selector';
import DataTable from '../parts/table/table';
import { connect } from 'react-redux'
import {CustomInputRange, CustomInputSlider} from '../parts/input_range/input_range';
import "./waiting_form.css";
import { useHistory } from 'react-router';
import {LangCode} from '../../common/LangCode'
import * as actions from '../../actions/langauge/waiting_list_lang';

const DataTableModel = [
    {
        date: "2021-02-10",
        program: ["Eye Diseas Presence","Diabetic Retinopathy"],
        birth: "1994-08-27",
        age : "26",
        gender: "F",
        basal: " 7",
        place: "Apollo Reach Hospital",
    },
    {
        date: "2021-02-10",
        program: ["Eye Diseas Presence", "Diabetic Retinopathy"],
        birth: "1994-08-27",
        age : "26",
        gender: "F",
        basal: " 7",
        place: "Apollo Reach Hospital",
    },
    {
        date: "2021-02-10",
        program: ["Eye Diseas Presence", "Diabetic Retinopathy"],
        birth: "1994-08-27",
        age : "26",
        gender: "F",
        basal: " 7",
        place: "Apollo Reach Hospital",
    }
]

const FilterLangModel = {
    age: "Age",
    program : "Program",
    basal_disease: "Basal disease",
    period : "Period",
    waiting : "Waiting",
    hold: "Hold"
}

const FilterModel = {
    ageValue: [0,100],
    periodValue : 0,
    waitingValue : true,
    holdValue : true,
}

const WaitingForm = (props) => {
    const [filterValue, setFilterValue] = useState(FilterModel);
    const [initFilterLang,setInitFilterLang] = useState(FilterLangModel);
    const updateFilterLang = props.filter;
    const updateListLang = props.list;

    const history = useHistory();

    useEffect(() => {
        if(updateFilterLang !== undefined){
            if(Object.keys(updateFilterLang).length !== 0){
                setInitFilterLang(updateFilterLang);
            }
        }
    })

    const handleRangeChange = (value) => {
        setFilterValue({
            ...filterValue,
            "ageValue" : value,
        })
    }

    const handleSliderChange = (value) => (
        setFilterValue({
            ...filterValue,
            "periodValue" : value,
        })
    )

    const handleWaitingChange = () => {
        filterValue.waitingValue ? 
        setFilterValue({
            ...filterValue,
            "waitingValue" : false,
        }) : 
        setFilterValue({
            ...filterValue,
            "waitingValue" : true,
        })
    }

    const handleHoldChange = () => {
        filterValue.holdValue ? setFilterValue({
            ...filterValue,
            "holdValue" : false,
        }) : setFilterValue({
            ...filterValue,
            "holdValue" : true,
        })
    }

    const Logout = () => {
        window.localStorage.setItem("isLogin","false");
        console.log(window.localStorage.getItem("isLogin"));
        window.location.reload();
        //history.push('/');
    }

    const FilterBox = (
        <div className = {'filter_type_div'}>
            <div className = {'select_value'}>
                <Selector title= {initFilterLang.program}/>
                <Selector title= {initFilterLang.basal_disease}/>
                <div className = 'range_slider_with_label'>
                    <span className = "range_sldier_title">
                        {initFilterLang.age}
                    </span>
                    <CustomInputRange 
                        min={0}
                        max={100}
                        onChange = {(value) => handleRangeChange(value)}
                        defaultValue = {filterValue.ageValue}/> 
                </div>
                <div className = 'range_slider_with_label'>
                    <span className = "range_sldier_title">
                        {initFilterLang.period}
                    </span>
                    <CustomInputSlider 
                        min={0}
                        max={12}
                        onChange = {(value) => handleSliderChange(value)}
                        defaultValue = {filterValue.periodValue}/> 
                </div>
                <CheckBox checked={filterValue.waitingValue} title = {initFilterLang.waiting}
                    onChange = {handleWaitingChange} />
                <CheckBox checked={filterValue.holdValue} title = {initFilterLang.hold} onChange = {handleHoldChange} />
            </div>
        </div>
    )

    const SavedFilterBox = (
        <div className = 'saved_filter_div'>
            <div className = {'select_value'}>
                <Selector title= 'Filter'/>
            </div>
            <NormalButton title = 'Interpretation' disable = {false} width = {'200px'} onClick = {Logout}/>  
        </div>
    )

    const infoTable = (
        <DataTable tableBodyList = {DataTableModel} list = {updateListLang}/>
    )

    const PagerBox = (
        <div className = 'pager_form_div'>
            <Pager 
                buttons = {["1","2","3","4","5"]}
                buttonsEvent = {(e,i) => console.log(i)}
            />
        </div>
    )

    return (
        <div className = {"waiting_form_div"}>
            {FilterBox}
            {SavedFilterBox}
            {infoTable}
            {PagerBox}
        </div>
    )
}

const mapStateToProps = (state) => ({
    //waitingListLang: state.LangReducer.waitingListLang
  })
  
  const mapDispatchToProps = {
    //getWaitingLang: actions.GetWaitingListLang,
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(WaitingForm)
  
