import React, {createContext,useState,useLayoutEffect} from 'react';
import {BrowserRouter as Router,Switch,Route,Link, Redirect,useRouteMatch, useParams} from "react-router-dom";
import Interpretation from './interpretation/interpretation';
import Waiting from './waiting/waiting';
import {QHD,HD} from "../../common/MediaQuery";
import Themes from "../../common/theme";
import Header from "../../components/header/header";
import * as actions from "../../actions/langauge/waiting_list_lang";
import { connect } from 'react-redux'

const ThemeContext = createContext(Themes.dark);

const HomeView = (props) =>{
  const [callbackIdValue, setCallbackIdValue] = useState(0);

  return (
    <Router basename = {'/eye_dx'}>
      <ThemeContext.Provider value = {callbackIdValue === 0 ? Themes.blue : Themes.dark}>
        <Header size = {props.size} themeContext = {ThemeContext} callBackId = {(id)=>setCallbackIdValue(id)} tab = {props.tab}/>
      </ThemeContext.Provider>
        <Switch>
          <Route exact path = "/" component = {Waiting}/>
          <Route exact path = "/interpretation" component = {Interpretation}/>
        </Switch>
    </Router>
  );
}

const ResponsHome = (props) => {

  useLayoutEffect(() => {
    //debugger;
    props.getWaitingLang({lang:"kr"});
  }, [])

  return (
    <div>
      <QHD>
        <HomeView size = "QHD" tab = {props.waitingListLang.tab}/>
      </QHD>
      <HD>
        <HomeView size = "HD" tab = {props.waitingListLang.tab}/>
      </HD>
    </div>
  )
}

const mapStateToProps = (state) => ({
  waitingListLang: state.LangReducer.waitingListLang
})

const mapDispatchToProps = {
  getWaitingLang: actions.GetWaitingListLang,
}

export default connect(mapStateToProps, mapDispatchToProps)(ResponsHome)
