import React, { useEffect, useState } from 'react';
import CustomLabel from '../label/label';
import "./table.css";

const ListLangModel = {
    order : "INSPECTION DATE",
    program : "PROGRAM",
    birth_date: "BIRTH DATE",
    gender : "GENDER",
    basal_disease : "BASAL DISEASE",
    place : "INSPECTION PLACE",
    status : "STATUS",
}

const TableHeader = (props) => {
    const list = props.list;

    return (
        <thead>
           <tr>
                <th>{list.order}</th>
                <th>{list.program}</th>
                <th>{list.birth_date}</th>
                <th>{list.gender}</th>
                <th>{list.basal_disease}</th>
                <th>{list.place}</th>
                <th>{list.status}</th>
            </tr> 
        </thead>
    )
}

const TableBodyList = (props) => {
    return (
        <tr className = 'table_body_list_tr'>
            <td>{props.date}</td> 
            {/* <td>2021-02-10</td> */}
            <td className = 'table_body_progam_td'>
                <div className = "table_progame_td_div">{props.programList.map((title,i) => (
                    <CustomLabel key = {i} title = {title}/>))}</div></td>
            {/* <td><CustomLabel title = {'aaaaaaaaa'}/></td>     */}
            <td>{props.birth} ({props.age})</td>
            <td>{props.gender}</td>
            <td>{props.basal}</td>
            <td>{props.place}</td>
            <td>Status</td>
        </tr>
    )
}

const DataTable = (props) => {

    const [initListLang , setInitListLang] = useState(ListLangModel);
    const updateListLang = props.list;

    useEffect(()=>{
        if(updateListLang !== undefined){
            if(Object.keys(updateListLang).length !== 0){
                setInitListLang(updateListLang);
            }
        }
    })

    return (
        <div className = 'table_wrapper'>
            <div className = 'tbody_background_div'/>
            <div className = 'table_div'>
                <table>
                <TableHeader list = {initListLang}/>
                <tbody className = 'tbody_value'>
                    {props.tableBodyList.map((props,i) => (
                        <TableBodyList key={i} 
                            date = {props.date}
                            programList = {props.program}
                            birth = {props.birth}
                            gender = {props.gender}
                            basal = {props.basal}
                            age = {props.age}
                            place = {props.place}/>
                    ))}
                </tbody>
            </table> 
            </div>
        </div>
    )
}

export default DataTable;
