import React, {useState, useRef} from 'react';
import "./image_zoom.css";
import Slider from '../slider/slider';
import {useDimensions} from "../../common/sizeTgarget";
import Eye from '../../images/eye.jpg';

const MousePosition = {
    position_x : 0,
    position_y : 0,
}

const OriginModel = {
    origin_x: 0,
    origin_y:0,
}

const DragModel = {
    start_x: 0,
    start_y: 0,
}

const DistanceModel = {
    distance_x: 0,
    distance_y: 0,
}

const currentImgPostionModel = {
    current_x: 0,
    current_y: 0,
}

const ImageSizedModel = {
    width: 0,
    height: 0,
}

const ImageZoom = () => {
    const targetRef = useRef();
    const size = useDimensions(targetRef); ///   
    const [changeImg, setChangeImg] = useState(ImageSizedModel); ///   

    const [position, setPosition] = useState(MousePosition); ///   
    const [origin, setOrigin] = useState(OriginModel); ///     
    const [scale, setScale] = useState(1); ///     

    const [isDrag, setIsDrag] = useState(false); ///     
    const [dragStart, setDragStart] = useState(DragModel); ///   
    const [distance, setDistance] = useState(DistanceModel); ///   
    const [current, setCurrent] = useState(currentImgPostionModel); ///  

    const [sliderOpacity, setSliderOpacity] = useState(0); ///  

    const handleWheelZoom = e => {
        // e.preventDefault();
        setSliderOpacity(1.0); 
        ///   => 
        if(e.deltaY < 0) {
            if(changeImg.width*1.08 >= 4032){
                setScale(scale);
                setChangeImg({
                    width: 4032,
                    height: 3020,
                });
            } else {
                setScale(scale*1.08);
                setChangeImg({
                    width: scale === 1 ? size.width : changeImg.width*1.08,
                    height: scale === 1 ? size.height : changeImg.height*1.08,
                });
            }
        }
        ///  
        if(e.deltaY > 0){
            /// 1.0     
            if(changeImg.width/1.08 < size.width) {
                setScale(1);
                ///     
                setDistance({
                    distance_x: 0,
                    distance_y: 0, 
                });
                setCurrent({
                    current_x:0,
                    current_y:0
                });
                setChangeImg({
                    width: 0,
                    height: 0,
                })
            } else {
                setScale(scale/1.08);
                setChangeImg({
                    width: changeImg.width/1.08,
                    height: changeImg.height/1.08,
                })
            }
        }
        setOrigin({
            origin_x: position.position_x,
            origin_y: position.position_y,
        });
        setTimeout(()=>{
            setSliderOpacity(0)
        },3000);
    }

    const handleMouseMove = e => {
        e.preventDefault();
        ///   
        if(e.clientX > (window.innerWidth - 350)/2) {
            setPosition({
                position_x:e.clientX - (window.innerWidth-350)/2,
                position_y:e.clientY - 60,
            });
        } else {
            setPosition({
                position_x:e.clientX,
                position_y:e.clientY - 60,
            });
        }
        ///    =>  
        if(isDrag){
            if(e.clientX > (window.innerWidth - 350)/2) {
                setDistance({
                    distance_x:current.current_x + ((e.clientX - (window.innerWidth-350)/2)-dragStart.start_x),
                    distance_y:current.current_y + ((e.clientY - 60) - dragStart.start_y),
                });
            } else {
                setDistance({
                    distance_x:current.current_x + (e.clientX-dragStart.start_x),
                    distance_y:current.current_y + ((e.clientY-60) - dragStart.start_y),
                });
            }
        }
    }

    /// 
    const handleDragStart = (e) => {
        e.preventDefault();
        if(scale === 1){
            setIsDrag(false); ///       isDrag == false
        } else {
            setIsDrag(true);
            if(e.clientX > (window.innerWidth - 350)/2) {
                setDragStart({
                    start_x: e.clientX - (window.innerWidth-350)/2,
                    start_y: e.clientY - 60,
                });
            } else {
                setDragStart({
                    start_x: e.clientX,
                    start_y: e.clientY - 60,
                });
            }
        }
    };
    
    /// 
    const handleDragEnd = (e) => {
        e.preventDefault();
        setIsDrag(false);
        setCurrent({
            current_x: distance.distance_x,
            current_y: distance.distance_y,
        });
    };

    ///      
    const handleMouseLeave = (e) => {
        e.preventDefault();
        setIsDrag(false);
    }

    ///   
    const hanldeDoubleClick = (e) => {
        setSliderOpacity(1.0);
        setScale(1.0);
        setDistance({
            distance_x: 0,
            distance_y: 0,
        });
        setCurrent({
            current_x: 0,
            current_y: 0,
        });
        setPosition({
            position_x: 0,
            position_y: 0,
        });
        setChangeImg({
            width: 0,
            height: 0,
        })
        setTimeout(()=>{
            if(sliderOpacity === 1.0) setSliderOpacity(0)
        },3000);
    }

    return(
        <div className = "image_zoom_wrap">
          <div className = "image_zoom_div"> 
            <img className = 'img_src' 
            alt=''
            ref = {targetRef}
            onWheel = {(e)=>handleWheelZoom(e)} 
            onMouseMove = {(e)=>handleMouseMove(e)}
            onMouseDown = {handleDragStart}
            onMouseUp = {(e)=>handleDragEnd(e)}
            onMouseLeave = {handleMouseLeave}
            onDoubleClick = {hanldeDoubleClick} 
            style = {{
                transform: `scale(${scale}) translate(${distance.distance_x+'px'}, ${distance.distance_y+'px'})`,
                transformOrigin: `${origin.origin_x+'px'} ${origin.origin_y+'px'}`}}
            src = {Eye}/>
          </div>
          <Slider opacity = {sliderOpacity}
            min = '0'
            max = {4032-size.width} 
            value = {changeImg.width === 0 ? changeImg.width : changeImg.width-size.width} 
            progress = {changeImg.width === 0 ? changeImg.width : (changeImg.width-size.width)/(4032-size.width)*100} 
            onChange={handleWheelZoom}/>
        </div>
    )
}

export default ImageZoom;