import React, { useState } from 'react';
import "./interpretation.css";
import ImageZoom from '../../../components/image_zoom/image_zoom'

const IsSelectedModel = {
    isOSSelected: true,
    isODSelected: false,
}

const Interpretation=()=>{
    const [isSelected, setIsSelected] = useState(IsSelectedModel);

    const handleOSToODChange = e => {
        setIsSelected({
            ...isSelected,
            "isOSSelected":false,
            "isODSelected":true,
        })
    }

    const handleODToOSChange = e => {
        setIsSelected({
            ...isSelected,
            "isOSSelected":true,
            "isODSelected":false,
        })
    }

    return  <div className = {'interpretation_body_QHD'}>
         <div onClick = {handleODToOSChange} className = {isSelected.isOSSelected? 'interpretation_photo_click' : 'interpretation_photo'}>
            {<ImageZoom></ImageZoom>}
        </div>
        <div onClick = {handleOSToODChange} className = {isSelected.isODSelected? 'interpretation_photo_click' : 'interpretation_photo'}>
            {<ImageZoom></ImageZoom>}
        </div>
        <div className = {'interpretation_list_QHD'}>
            this is list area
        </div>
    </div>
}

export default Interpretation
