import React, {useLayoutEffect} from 'react';
import {ReactComponent as Logo} from "../../images/logo_eyedx.svg";
import {QHD,HD} from "../../common/MediaQuery";
import "./login.css";
import LoginForm from '../../components/login_form/login_form';
import {LangCode} from '../../common/LangCode';
import { connect } from 'react-redux';
import * as actions from '../../actions/langauge/login_lang'

const LoginView = (props) => {

  return(
    <div className = {'login_view_header_'+props.size}>
      <div className = {'login_view_logo_div_'+props.size}>
        <Logo className = {"login_view_logo_"+props.size}/>
      </div>
      <div className = 'login_view_position'>
        <LoginForm size = {props.size} loginLang = {props.loginLang}/>
      </div>
    </div>
  );
}

const ResponsLogin = (props) => {
  
  useLayoutEffect(() => {
    props.getLoginLang({lang:"kr"});
  }, [])

  return (
    <div>
      <QHD>
        <LoginView size = "QHD" loginLang = {props.loginLangs}></LoginView>
      </QHD>
      <HD>
        <LoginView size = "HD" loginLang = {props.loginLangs}></LoginView>
      </HD>
    </div>
  );
}

const mapStateToProps = (state) => ({
  loginLangs: state.LangReducer.loginLang
})

const mapDispatchToProps = {
  getLoginLang: actions.GetLoginLang,
}

export default connect(mapStateToProps, mapDispatchToProps)(ResponsLogin);