import React,{useContext,useState,useEffect} from 'react';
import { connect } from 'react-redux'
import {ReactComponent as Logo} from "../../images/logo_eyedx.svg";
import {BrowserRouter as Router,Switch,Route,Link, Redirect,useRouteMatch} from "react-router-dom";
import "./header.css";
import {LangCode} from '../../common/LangCode'
import * as actions from '../../actions/langauge/waiting_list_lang';

const tabLangModel = {
  waiting : "Waiting",
  interpretation : "Interpretation",
}

const TabButton = (props) => {
  const theme = props.theme; 
  const [id, setId] = useState(0);

  let match = useRouteMatch({
    path : props.to,
    exact : props.activeOnlyWhenExact
  });

    return (
      <Link to = {props.to} style = {{
        color: `${match ? theme.mainColor : "#9F9F9F"}`,
  }}>
        <button 
          className = {match ? 'home_tab_active_'+props.size : 'home_tab_inactive_'+props.size}
          style = {{
            borderBottom:`3px solid ${match ? theme.mainColor : 'transparent'}`,
            color:`${match ? theme.mainColor : '#9F9F9F'}`,
          }}
          onClick = {()=>{
            setId(props.id);
            props.callBackId(props.id);
            console.log(props.id);
          }}>
          {props.title}
        </button></Link>
    )
}

const Header = (props) => {
  const theme = useContext(props.themeContext); 
  const [initTabLang, setInitTabLang] = useState(tabLangModel);
  const updateTabLang = props.tab;

  useEffect(() => {
    //debugger;
    if(updateTabLang !== undefined){
      if(Object.keys(updateTabLang).length !== 0){
        setInitTabLang(updateTabLang);
    }
  }
    //debugger;
    //props.getWaitingLang({lang:"kr"});
  })

  return (
      <div className = {'header_'+props.size} 
        style = {{
          backgroundColor: theme.backgroundColor,
          borderBottom:theme.border 
        }}>
        <div className = {'header_logo_div_'+props.size}
        style = {{
          backgroundColor:theme.backgroundColor,
        }}>
          <Logo className = {'header_logo_'+props.size} fill = {theme.mainColor}/>
        </div> 
          <ul className = {'tabs_ul'}>
              <TabButton id = {0} size = {props.size} theme = {theme} activeOnlyWhenExact = {true} to = "/" title = {initTabLang.waiting} callBackId = {props.callBackId} /> 
              <TabButton id = {1} size = {props.size} theme = {theme} to = '/interpretation' title = {initTabLang.interpretation} callBackId = {props.callBackId}/>
          </ul>
        <div className = {'header_login_info_div_'+props.size}
          style = {{
            borderLeft: theme.border,
          }}>
          <p>ID: SKR123001</p>
        </div>
      </div> 
  )
}

